.section.icon-grid .container {
    padding: 35px 0;
}
@media (min-width: $(desktop)) {
    .section.icon-grid .container {
        padding: 100px;
    }
}
.section.icon-grid .container ul {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
    margin-right: calc(99.99% * 1/24 - (30px - 30px * 1/24) + (30px * 2)) !important;
    margin-left: calc(99.99% * (-1/24 * -1) - (30px - 30px * (-1/24 * -1)) + 30px) !important;
}
.section.icon-grid .container li {
    width: 100%;
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    height: calc(99.99% * 2/4 - (30px - 30px * 2/4));
    margin-bottom: 30px;
    flex: 0 0 auto;
    width: calc(99.99% * 2/4 - (30px - 30px * 2/4));
}
.section.icon-grid .container li:last-child {
    margin-bottom: 0;
}
.section.icon-grid .container li:nth-child(1n) {
    margin-right: 30px;
    margin-left: 0;
}
.section.icon-grid .container li:last-child {
    margin-right: 0;
}
.section.icon-grid .container li:nth-child(2n) {
    margin-right: 0;
    margin-left: auto;
}
@media (min-width: $(desktop)) {
    .section.icon-grid .container li {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 1/4 - (30px - 30px * 1/4));
    }
    .section.icon-grid .container li:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.icon-grid .container li:last-child {
        margin-right: 0;
    }
    .section.icon-grid .container li:nth-child(4 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.section.icon-grid .container li:last-child,
            .section.icon-grid .container li:nth-last-child(2) {
    margin-bottom: 0;
}
.section.icon-grid .container .image {
    background: $green;
    border-radius: 50%;
    padding: 20px;
    width: 90px;
    height: 90px;
    display: block;
    margin: 0 auto;
    margin-bottom: 20px;
    -webkit-transition: all 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: all 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.section.icon-grid .container .icon::before {
    font-size: 48px;
    color: $white;
    font-weight: normal;
}
.section.icon-grid .container .text {
    width: 140px;
    margin: 0 auto;
    position: relative;
    -webkit-transition: -webkit-transform 300ms 75ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: -webkit-transform 300ms 75ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: transform 300ms 75ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: transform 300ms 75ms cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 300ms 75ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    font-family: 'Open Sans';
}
.section.icon-grid .container li {
    text-align: center;
}
.section.icon-grid .container a {
    text-decoration: none;
    color: $green;
    font-weight: 600;
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
}
.section.icon-grid .container a:hover .image {
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    box-shadow: 0 0 0 4px $green,
        inset 0 0 0 3px $white;
    color: #fff;
}
